@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-weight: bold;
  font-style: normal;
}
body {
  min-width: 320px;
  margin: auto;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #111111;
}
.page-inner {
  width: 320px;
  margin: 0 auto;
  padding: 0;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .page-inner {
    width: 768px;
  }
}
@media (min-width: 1440px) {
  .page-inner {
    width: 1440px;
  }
}
.button {
  display: flex;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;
  background: #68b738;
  text-decoration: none;
}
.button__text {
  display: inline-block;
  margin: auto;
}
.button:hover {
  background: #5eaa2f;
}
.button:active {
  background: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button--gray {
  background: #f2f2f2;
  color: #444444;
  font-size: 16px;
}
.button--gray:hover {
  background: #ebebeb;
}
.button--gray:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
